#navbar{
    display: flex;
    box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
}
#navbar img{
    width: 130px;
    padding-left: 20px;
}
#body img{
    width: 550px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 30px;
}
#body h1, p{
    padding-left: 10px;
    font-family: "Poppins", sans-serif;
}
#body h1{
    margin-top: -30px;
}
.cardImage{
    width: 265px;
    height: 265px;
    object-fit: cover;
    border-radius: 9px;
}
.icon{
    width: 15px;
    height: 15px;
    margin-top: 20px;
}
.card span{
    display: flex;
    margin-bottom: -20px;
    margin-left: 10px;
}
#Cards{
    display: flex;
    justify-content: space-evenly;
}
.card{
    position: relative;
}
.punctuation :last-child{
    color: #918E9B;
}
.soldOut{
    position: absolute;
    top: 6px;
    left: 6px;
    background-color: white;
    padding: 5px 7px;
    border-radius: 2px;
    font-weight: bold;
}
